import { mapMutations, mapGetters } from 'vuex';
import del from '@/static/mbcImg/my/del.png'
export default {
	data() {
		return {
			lists:[],
			hidens:2,
			del:del,
			nums:0,
			edits:'',
			blurType: '1', // 代表默认失去焦点事件，2代表点击添加时失去焦点事件
		};
	},
	components: {

	},
	computed: {
		i18n() {
			return this.$t('Mypersonal');
		},
		...mapGetters(['GET_HOME', 'QUICKNAVCO','GET_MY'])
	},
	onLoad() {
		uni.setNavigationBarTitle({
			title: this.i18n.title1
		});
	},
	created() {
		this.Purchase();
	},
	methods: {
		Purchase(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/user/760', //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.lists = response.data.content.instKeys
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		dele(e){
			console.log(e)
			this.lists.splice(e,1)
		},
		Preservation(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				if(this.lists.length===0){
					this.lists.push('')
					var params = {
						"instKeys":this.lists,
					}; // 请求总数居时 参数为空
				}else{
					var params = {
						"instKeys":this.lists,
					}; // 请求总数居时 参数为空
				}
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/user/760', //接口地址。
					data: params,
					method: 'POST',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.GET_MY.headers.instKeys = this.lists;
						this.$store.commit('setMy', this.GET_MY.headers); // 更新vuex
						uni.navigateBack({});
						// this.$store.commit('setMation', this.List); // 更新vuex
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		adds(){
			this.hidens = '1';
			this.nums = '1';
			console.log('创建新标签');
		},
		subs(){
			if(this.edits === ''){ // 失脚时没有数据
				this.hidens = 2
			}else{ // 失焦时存在数据
				this.hidens = 2
				this.lists.push(this.edits);
				this.edits = '';
			}
		}
	}
};
